/* RegisterPage.css */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.6;
  color: #333;
}

.register-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #eff6ff 0%, #e0e7ff 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3rem 1rem;
}

.register-container {
  max-width: 36rem;
  width: 100%;
}

.register-header {
  text-align: center;
  margin-bottom: 2rem;
}

.back-button {
  display: inline-flex;
  align-items: center;
  color: #4f46e5;
  margin-bottom: 1.5rem;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s;
}

.back-button:hover {
  color: #4338ca;
}

.back-icon {
  margin-right: 0.5rem;
}

.register-title {
  font-size: 1.875rem;
  font-weight: 700;
  color: #1f2937;
}

.register-subtitle {
  margin-top: 0.5rem;
  font-size: 0.875rem;
  color: #6b7280;
}

.register-form-container {
  background-color: white;
  padding: 2rem 1.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.register-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.error-message {
  background-color: #fef2f2;
  border: 1px solid #fee2e2;
  color: #dc2626;
  padding: 0.75rem 1rem;
  border-radius: 0.375rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

@media (min-width: 640px) {
  .form-row {
    grid-template-columns: repeat(2, 1fr);
  }
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.25rem;
}

.form-input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-input:focus {
  outline: none;
  border-color: #4f46e5;
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.2);
}

.form-input.error {
  border-color: #ef4444;
}

.input-error {
  margin-top: 0.5rem;
  font-size: 0.875rem;
  color: #dc2626;
}

.input-hint {
  margin-top: 0.25rem;
  font-size: 0.75rem;
  color: #6b7280;
}

.password-input-container {
  position: relative;
}

.password-toggle-button {
  position: absolute;
  top: 50%;
  right: 0.75rem;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  color: #9ca3af;
}

.password-strength {
  margin-top: 0.5rem;
}

.password-criteria {
  font-size: 0.75rem;
  color: #6b7280;
  margin-bottom: 0.25rem;
}

.criteria-list {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.criteria-item {
  display: flex;
  align-items: center;
  font-size: 0.75rem;
}

.criteria-valid {
  color: #10b981;
}

.criteria-invalid {
  color: #ef4444;
}

.criteria-icon {
  margin-right: 0.25rem;
}

.terms-checkbox {
  display: flex;
  align-items: flex-start;
}

.terms-input {
  width: 1rem;
  height: 1rem;
  margin-top: 0.25rem;
  margin-right: 0.5rem;
  color: #4f46e5;
  border: 1px solid #d1d5db;
  border-radius: 0.25rem;
}

.terms-label {
  font-size: 0.875rem;
  color: #1f2937;
}

.terms-link {
  color: #4f46e5;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: color 0.3s;
}

.terms-link:hover {
  color: #4338ca;
}

.register-button {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: white;
  background-color: #4f46e5;
  cursor: pointer;
  transition: background-color 0.3s;
}

.register-button:hover:not(:disabled) {
  background-color: #4338ca;
}

.register-button:disabled {
  background-color: #818cf8;
  cursor: not-allowed;
}

.loading-icon {
  animation: spin 1s linear infinite;
  margin-right: 0.75rem;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.divider {
  position: relative;
  margin: 1.5rem 0;
}

.divider-line {
  width: 100%;
  height: 1px;
  background-color: #e5e7eb;
}

.divider-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 0 0.5rem;
  font-size: 0.875rem;
  color: #6b7280;
}

.oauth-button {
  width: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: #6b7280;
  background-color: white;
  transition: background-color 0.3s;
  cursor: pointer;
}

.oauth-button:hover {
  background-color: #f9fafb;
}

.oauth-icon {
  margin-right: 0.5rem;
}

.login-prompt {
  margin-top: 1.5rem;
  text-align: center;
  font-size: 0.875rem;
  color: #6b7280;
}

.login-link {
  color: #4f46e5;
  font-weight: 500;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s;
}

.login-link:hover {
  color: #4338ca;
}
